<template>
  <section id="content">
    <h1>{{content.title}}</h1>
    <h3>{{content.contentText}}</h3>
  </section>
</template>

<script>
  import api from '../../../api/api'
  export default {
    name: 'SectionContent',
    props: ['contentId'],
    data: () => ({
      content: {}
    }),
    computed: {
      maxHeight () {
        const height = this.$vuetify.breakpoint.mdAndUp ? '30vh' : '20vh'
        return `calc(${height} - ${this.$vuetify.application.top}px)`
      },
    },
    mounted (){
      api.Content.getById(this.$route.params.contentId)
        .then(result => {
          this.content = result;
        });
    }
  }
</script>
